.divTecnologias {
  width: 100%;
  margin: auto auto;
  height: auto;
  background-color: var(--cor2);
}

.tecnologiasItem {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.divImgTecnologias {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
}

.nomeTecnologias {
  font-family: var(--fonte2);
  font-weight: 900;
  padding: 10px 0 0 0;
}

.imagensTecnologias {
  width: 140px;
  height: auto;
  padding: 0 20px;
  background: transparent;
  filter: drop-shadow(3px 3px 3px #1f1f1f);
}

.tituloTecnologias {
  width: 100%;
  font-weight: bold;
  font-family: var(--fonte2);
  color: var(--cor1);
  font-size: 30px;
  text-align: center;
  padding: 40px 0;
}
@media screen and (max-width: 840px) {

  .tituloTecnologias{
    font-size: 1.5rem;
  }

  .imagensTecnologias{
    width: 100px;
  }

}